@import url(https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans:wght@300&display=swap);
:root {
  --DarkGray:  hsl(0, 0%, 55%);
  --VeryDarkGray: hsl(0, 0%, 41%);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Josefin Sans;
}
.App {
    position: relative;
}

.topside-container {
    height: 100vh;
    background: url(/static/media/image-hero.9b5aacea.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
.menu-logo{
   position: absolute;
   width: 100%;
   top: 1rem;
}
.logo{
    float:left;
    padding: 1rem;
}
.menu-icon{
    float:right;
     padding-top: 1.5rem;
     padding-right: 1rem;
}
.desktop-options{
    color: white;
    display: -webkit-flex;
    display: flex;
}
.desktop-options h3{
    padding: 1rem 0.8rem;
}
.desktop-menu{
    position: absolute;
    top: 3rem; 
    right: 10%;
    display: none;
}
.topside-text{
    padding: 1rem;
    margin-left:1rem ;
    margin-right:1rem ;
    border: 3px solid white;
    color: white;
    text-transform: uppercase;
    position: relative;
    top: 30vh;
    font-family: Josefin Sans;
    font-size: 1.18rem;
    font-weight: 300;
    max-width: 30ch;
}
@media screen and (min-width: 1000px) {
    .topside-text{
        max-width: 50%;
        margin-left: 15% ;
        font-size: 2rem;
    }
    .menu-logo{
        margin-left: 15% ;
        margin-top: 2rem;
    }
    .topside-text h1{ 
        padding: 2rem;
    }
    .desktop-menu{
        display: block;
    }
}
.mobile-menu {
    background-color: black;
    min-height: 100vh;
    -webkit-align-items: center;
            align-items: center;

}
.menu-options {
    text-transform: uppercase;
     color: white;
     padding-left: 1rem;
}
.menu-options h3{
     padding: 0.5rem 0.2rem;
}
#IntroSection {
    background-color: white;
    padding-top: 3rem;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 3rem;
}
.interactive-img {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    width: 100%;
    height: auto;
}
.interactive-header{
    font-size: 2.3rem;
    text-transform: uppercase;
    text-align: center;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}
.interactive-text{
    padding-top: 2rem;
    color : var(--DarkGray);
    font-size: 1.4rem;
    padding-right: 1rem;
    padding-left: 1rem;
    text-align: center;
}
@media screen and (min-width: 1000px) {
    .interactive-img {
        width: 100%;
        position: relative;
        left: 10%;
    }
    #IntroSection{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .interactive-header-container{
        background-color: white;
        width: 90%;
        position: relative;
        right: 10%;
        top: 40%;
    }
    .interactive-header{
        padding: 2.5rem;
        text-align: start;
    }
    .interactive-text{
        padding: 2.5rem;
        padding-top: 1rem;
        text-align: start;
    }
}
#creations {
  padding-top: 3rem;
  padding-bottom: 2rem;
}
.creations-container {
    display: grid;
    grid-auto-rows: auto;
}
.creation{
    width: 90%;
    margin: 0.75rem auto;
    position: relative;
}
.creation-img {
    width: 100%;
}
.creation-title{
    text-transform: uppercase;
    color: white;
    position: absolute;
    bottom: 2rem;
    left: 1.6rem;
    max-width: 9ch; 
    font-size: 1.4rem;
}
#creations .header{
   font-size: 2.2rem;
   text-transform: uppercase;
   text-align: center;
   padding-bottom: 2rem;
}
#creations .more  {
    font-size: 1.4rem;
    text-transform: uppercase;
    text-align: center;
    text-justify: center;
    width: 50%;
    padding: 0.75rem;
    margin: 2rem auto;
    border: 2px solid var(--VeryDarkGray);
}
:hover .more, .creation {
    cursor: pointer;
}
@media screen and (min-width: 1000px) {
    #creations .header {
        text-align: start;
        padding-left: 4rem;
    }
    #creations{
        position: relative;
        margin-top: 5rem;
    }
    #creations .more{
        position: absolute;
        max-width: 10ch;
        top: 0;
        right: 7%;
    }
    .creations-container {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        width: 90%;
        margin: 3rem auto;
    }
    .creation {
        min-height: 400px;
    }
    .creation-img {
       height: 100%;
    }
    
}
#footer {
    min-height: 100vh;
    background-color: black;
    display: grid;
    grid-template-rows: 6fr 4fr;
    grid-template-columns: 1fr;
    place-items: center;
}
.logo-links{
    padding-top: 2rem;
}
.footer-logo {
    padding-bottom: 2rem;
}
.footer-links h3{
    color: white;
    text-align: center;
    padding: 0.8rem;
}
.copyright {
    color: var(--VeryDarkGray);
    font-size: 1.2rem;
    text-align: center;
    margin-top: 2rem;
}
.social-icon{
    margin-top: 1rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}
.icons-copyright{
    display: grid;
    place-items: center;
}
@media screen and (min-width: 1000px) {
    #footer {
        min-height: 5rem;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr;
        padding-bottom: 2rem;
    }
    .footer-links{
        display: -webkit-flex;
        display: flex;
    }
    .footer-links h3{
        text-align: start;
        padding: 0.5rem;
    }
    .footer-logo{
        padding-bottom: 0.5rem;
    }
    .icons-copyright{
        place-items: end;
    }
}
